@include font(Poppins-Bold, Poppins-Bold);
@include font(Poppins-Light, Poppins-Light);
@include font(Poppins-Medium, Poppins-Medium);
@include font(Poppins-Regular, Poppins-Regular);
@include font(Poppins-SemiBold, Poppins-SemiBold);
@include font(Poppins-Italic, Poppins-Italic);
@include font(Poppins-MediumItalic, Poppins-MediumItalic);
@include font(Poppins-ExtraBold, Poppins-ExtraBold);

@mixin extraBold {
	font-family: Poppins-ExtraBold, Arial, sans-serif;
	font-weight: 800; }

@mixin bold {
	font-family: Poppins-Bold, Arial, sans-serif;
	font-weight: 700; }

@mixin italic {
	font-family: Poppins-Italic, Arial, sans-serif;
	font-weight: 400; }

@mixin mediumItalic {
	font-family: Poppins-MediumItalic, Arial, sans-serif;
	font-weight: 500; }

@mixin light {
	font-family: Poppins-Light, Arial, sans-serif;
	font-weight: 300; }

@mixin medium {
	font-family: Poppins-Medium, Arial, sans-serif;
	font-weight: 500; }

@mixin regular {
	font-family: Poppins-Regular, Arial, sans-serif;
	font-weight: 400; }

@mixin semi {
	font-family: Poppins-SemiBold, Arial, sans-serif;
	font-weight: 600; }

body {
	line-height: 1.875;
	@include regular;
	color: $textColor;
	background-color: $pageBg;
	font-size: 16px;
	@include md {
		font-size: 12px; }
	&.is-overflow {
		overflow: hidden; } }

a {
	text-decoration: none;
	color: inherit;
	font-size: inherit; }

.out {
	overflow: hidden; }

.theme-bg {
	background-color: $themeColor; }

.white-color {
	color: $white;
	a {
		transition: .2s ease;
		&:hover {
			opacity: .8; } } }

.slider-btn {
	background: transparent;
	padding: 5px;
	&:hover {
		.icon {
			fill: $heroBtnColor; } }
	.icon {
		transition: 0.3s;
		fill: $white;
		font-size: 32px; } }

.slider-prev {
	.icon {
		transform: rotate(180deg);
		fill: $themeColor; } }

.inner-page {
	padding-top: 123px;
	@include sm {
		padding-top: 100px; } }
