.schedule {
	&__links {
		display: flex; }
	&__link {
		width: 50%;
		text-align: center;
		font-size: 20px;
		@include bold;
		border-bottom: 1px solid #C8C8C8;
		padding-bottom: 15px;
		@include sm {
			font-size: 16px;
			padding-bottom: 5px;
			@include regular; }
		&.is-active {
			color: $themeColor;
			border-color: $themeColor;
			border-width: 2px; } }
	&__tab {
		display: none;
		padding: 80px 0 20px;
		@include sm {
			padding: 40px 0 10px; }
		&.is-active {
			display: block; } }
	&__item {
		&:not(:last-child) {
			margin-bottom: 40px;
			@include sm {
				margin-bottom: 20px; } }
		& .schedule__label {
				margin-top: 15px; } } }

.sitem {
	display: flex;
	@include xs {
		display: block; }
	&__time {
		min-width: 100px;
		@include medium;
		font-size: 36px;
		margin-right: 20px;
		line-height: 1.2;
		@include sm {
			font-size: 20px;
			min-width: 60px;
			margin-right: 10px; }
		@include xs {
			margin-bottom: 8px; } }
	&__title, &__subtitle {
		font-size: 24px;
		margin-bottom: 10px;
		line-height: 1.25;
		cursor: pointer;
		transition: .3s ease;
		@include bold;
		@include sm {
			font-size: 16px; }
		a {
		  text-decoration: underline; }
		&:hover:not(.sitem__subtitle), &.is-active {
			color: #718E9D; } }

	&__speaker {
		font-size: 20px;
		@include sm {
			font-size: 15px; } }

	&__about {
		display: none; }

	&__from {
		font-size: 20px;
		margin-bottom: 30px;
		margin-top: 10px;
		@include sm {
			font-size: 15px;
			margin-top: 0; } }

	&__text {
		font-size: 20px;
		line-height: 1.5;
		@include sm {
			font-size: 15px; } }

	&__child {
		&:first-child {
			padding-top: 40px; }
		&:not(:last-child) {
			margin-bottom: 30px; } } }
