  margin-bottom: 8px;
}

.burger span:nth-child(1) {
  width: 22px;
  transform: translateX(5px);
}

.burger span:nth-child(1):before {
  left: -5px;
}

.burger span:nth-child(2) {
  width: 12px;
  transform: translateX(10px);
}

.burger span:nth-child(2):before {
  left: -10px;
  box-shadow: 5px 0 0 0 #ffffff;
}

.burger span:nth-child(2):after {
  right: -5px;
}

.burger span:nth-child(3) {
  width: 18px;
  transform: translateX(-1px);
}

.burger span:nth-child(3):before {
  right: -5px;
}

.burger span:nth-child(3):after {
  right: -10px;
}

.burger.is-active span {
  width: 100%;
}

.burger.is-active span:after, .burger.is-active span:before {
  content: none;
}

.burger.is-active span:nth-child(1) {
  transform: translate(0px, 10px) rotate(45deg);
}

