  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

@media only screen and (max-width: 1300px) {
  .location__info {
    max-width: 900px;
  }
}

@media only screen and (max-width: 1200px) {
  .location__info {
    max-width: 540px;
  }
}

@media only screen and (max-width: 1023px) {
  .location__info {
    z-index: 2;
    left: 50%;
    transform: translateX(-50%);
  }
}

@media only screen and (max-width: 599px) {
  .location__info {
    margin: 0 10px;
    position: relative;
    left: 0;
    width: auto;
    transform: translateY(91px);
  }
}

.location__visual {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 49%;
  width: 100%;
}

@media only screen and (max-width: 1300px) {
  .location__visual {
    padding-top: 60%;
  }
}

@media only screen and (max-width: 1023px) {
  .location__visual {
    padding-top: 50%;
  }
}

.location__label {
  position: absolute;
  max-width: 295px;
  top: -41px;
  right: -130px;
}

@media only screen and (max-width: 1023px) {
  .location__label {
    max-width: 140px;
    top: -36px;
    right: -74px;
  }
}

@media only screen and (max-width: 767px) {
  .location__label {
    right: -20px;
  }
}

@media only screen and (max-width: 599px) {
  .location__label {
    right: -10px;
    top: -40px;
  }
}

.location__label:after {
  content: '';
  position: absolute;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url(../img/label-dots.svg);
  height: 63px;
  width: 63px;
  right: -37px;
  bottom: -38px;
}

@media only screen and (max-width: 1023px) {
  .location__label:after {
    top: -200px;
    right: 250px;
  }
}

@media only screen and (max-width: 599px) {
  .location__label:after {
    top: -110px;
    right: 80px;
  }
}

.location__form {
  position: absolute;
  left: 134px;
  bottom: -100px;
  max-width: 570px;
  width: 100%;
  z-index: 1;
}

@media only screen and (max-width: 1200px) {
  .location__form {
    max-width: none;
    width: 100%;
    bottom: 0;
    transform: translateY(100%);
    left: 0;
  }
}

@media only screen and (max-width: 599px) {
  .location__form {
    transform: none;
    position: relative;
  }
}

.llabel {
  z-index: 1;
  position: relative;
  padding: 30px 35px 30px 40px;
  background-color: #fff;
  font-size: 20px;
  font-family: Poppins-Medium, Arial, sans-serif;
  font-weight: 500;
  line-height: 2;
}

@media only screen and (max-width: 1200px) {
  .llabel {
    font-size: 16px;
    padding: 30px;
  }
}

@media only screen and (max-width: 1023px) {
  .llabel {
    font-size: 10px;
    line-height: 1.8;
    padding: 13px 15px;
  }
}

.llabel__title {
  font-family: Poppins-Bold, Arial, sans-serif;
  font-weight: 700;
}

.llabel a {
  text-decoration: underline;
}

.llabel a:hover {
  text-decoration: none;
}

.sform {
  padding: 25px 40px 40px;
  background-color: #fff;
}

@media only screen and (max-width: 1023px) {
  .sform {
    padding: 26px 25px 30px;
  }
}

@media only screen and (max-width: 599px) {
  .sform {
    padding: 15px 20px 20px;
  }
}

.sform__title {
  font-size: 36px;
  font-family: Poppins-SemiBold, Arial, sans-serif;
  font-weight: 600;
  margin-bottom: 4px;
}

@media only screen and (max-width: 1023px) {
  .sform__title {
    font-size: 20px;
    margin-bottom: 14px;
  }
}

@media only screen and (max-width: 599px) {
  .sform__title {
    margin-bottom: 7px;
    margin-left: 10px;
  }
}

.sform__field {
  height: 60px;
  width: 100%;
  position: relative;
}

@media only screen and (max-width: 599px) {
  .sform__field {
    height: auto;
  }
}

.sform__input {
  width: 100%;
  height: 100%;
  font-size: 14px;
  border: 1px solid #D7D7D7;
  padding: 0 150px 0 20px;
  letter-spacing: 0.5px;
}

@media only screen and (max-width: 599px) {
  .sform__input {
    border-width: 0 0 1px 0;
    padding: 0 10px;
    text-align: center;
    height: 40px;
  }
}

.sform__btn {
  z-index: 1;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
}

@media only screen and (max-width: 599px) {
  .sform__btn {
    display: flex;
    justify-content: center;
    position: static;
    transform: translateY(0);
    margin-top: 20px;
    top: 0;
  }
}

.sform__social {
  margin-bottom: 15px;
}

@media only screen and (max-width: 1023px) {
  .sform__social {
    margin-bottom: 13px;
  }
}

@media only screen and (max-width: 599px) {
  .sform__social {
    margin-bottom: 11px;
  }
}

.ssocial {
  display: flex;
}

@media only screen and (max-width: 599px) {
  .ssocial {
    flex-wrap: wrap;
  }
}

@media only screen and (max-width: 599px) {
  .ssocial__item {
    width: 50%;
  }
}

.ssocial__item:not(:last-child) {
  margin-right: 35px;
}

@media only screen and (max-width: 599px) {
  .ssocial__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.ssocial__link {
  display: flex;
  align-items: center;
  color: #230CEA;
  font-family: Poppins-Light, Arial, sans-serif;
  font-weight: 300;
  font-size: 14px;
}

.ssocial__link:hover .icon {
  transform: scale(1.1);
}

.ssocial__icon {
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 7px;
}

.ssocial__icon .icon {
  transition: 0.3s;
  fill: #230CEA;
  font-size: 12px;
}

.schedule__links {
  display: flex;
}

.schedule__link {
  width: 50%;
  text-align: center;
  font-size: 20px;
  font-family: Poppins-Bold, Arial, sans-serif;
  font-weight: 700;
  border-bottom: 1px solid #C8C8C8;
  padding-bottom: 15px;
}

@media only screen and (max-width: 767px) {
  .schedule__link {
    font-size: 16px;
    padding-bottom: 5px;
    font-family: Poppins-Regular, Arial, sans-serif;
    font-weight: 400;
  }
}

.schedule__link.is-active {
  color: #230CEA;
  border-color: #230CEA;
  border-width: 2px;
}

.schedule__tab {
  display: none;
  padding: 80px 0 20px;
}

@media only screen and (max-width: 767px) {
  .schedule__tab {
    padding: 40px 0 10px;
  }
}

.schedule__tab.is-active {
  display: block;
}

.schedule__item:not(:last-child) {
  margin-bottom: 40px;
}

@media only screen and (max-width: 767px) {
  .schedule__item:not(:last-child) {
    margin-bottom: 20px;
  }
}

.schedule__item .schedule__label {
  margin-top: 15px;
}

.sitem {
  display: flex;
}

@media only screen and (max-width: 599px) {
  .sitem {
    display: block;
  }
}

.sitem__time {
  min-width: 100px;
  font-family: Poppins-Medium, Arial, sans-serif;
  font-weight: 500;
  font-size: 36px;
  margin-right: 20px;
  line-height: 1.2;
}

@media only screen and (max-width: 767px) {
  .sitem__time {
    font-size: 20px;
    min-width: 60px;
    margin-right: 10px;
  }
}

@media only screen and (max-width: 599px) {
  .sitem__time {
    margin-bottom: 8px;
  }
}

.sitem__title, .sitem__subtitle {
  font-size: 24px;
  margin-bottom: 10px;
  line-height: 1.25;
  cursor: pointer;
  transition: .3s ease;
  font-family: Poppins-Bold, Arial, sans-serif;
  font-weight: 700;
}

@media only screen and (max-width: 767px) {
  .sitem__title, .sitem__subtitle {
    font-size: 16px;
  }
}

.sitem__title a, .sitem__subtitle a {
  text-decoration: underline;
}

.sitem__title:hover:not(.sitem__subtitle), .sitem__title.is-active, .sitem__subtitle:hover:not(.sitem__subtitle), .sitem__subtitle.is-active {
  color: #718E9D;
}

.sitem__speaker {
  font-size: 20px;
}

@media only screen and (max-width: 767px) {
  .sitem__speaker {
    font-size: 15px;
  }
}

.sitem__about {
  display: none;
}

.sitem__from {
  font-size: 20px;
  margin-bottom: 30px;
  margin-top: 10px;
}

@media only screen and (max-width: 767px) {
  .sitem__from {
    font-size: 15px;
    margin-top: 0;
  }
}

.sitem__text {
  font-size: 20px;
  line-height: 1.5;
}

@media only screen and (max-width: 767px) {
  .sitem__text {
    font-size: 15px;
  }
}

.sitem__child:first-child {
  padding-top: 40px;
}

.sitem__child:not(:last-child) {
  margin-bottom: 30px;
}

.cards {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  padding-top: 56.25%;
}

.cards__slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.cards__slider .slick-list, .cards__slider .slick-slide, .cards__slider .slick-track {
  height: 100%;
}

.card {
  overflow: hidden;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.card__speaker-photo {
  display: flex;
  align-items: flex-end;
  width: 42.9%;
}

.card__speaker-photo img {
  width: 85.6%;
}

.card__content {
  width: 57.1%;
  color: #fff;
  font-size: 3.1vw;
  margin-top: 5.8%;
}

.card__theme {
  font-size: 1.7em;
  font-family: Poppins-ExtraBold, Arial, sans-serif;
  font-weight: 800;
  width: 90%;
  line-height: 1.2;
  margin-bottom: 2.1vw;
}

.card__speaker-name {
  font-size: 1.35em;
  line-height: 1.2;
  margin-bottom: 1.2vw;
}

.card__speaker-position {
  font-size: 0.84em;
  line-height: 1.2;
  font-family: Poppins-Medium, Arial, sans-serif;
  font-weight: 500;
}

.faq {
  padding: 0;
  margin: 0 auto;
  list-style: none;
  max-width: 970px;
  background: #fff;
}

.faq.marged {
  margin-top: 52px;
}

.faq__section-title {
  font-size: 24px;
  line-height: 1.36;
  margin: 40px 0 20px;
  font-weight: 700;
}

.faq__arrow {
  content: '';
  position: absolute;
  top: 28px;
  right: 30px;
  width: 7px;
  height: 12px;
  transform: rotate(90deg);
  transition: all 0.5s;
}

.faq__item {
  position: relative;
  border: 2px solid #230cea;
  margin: 0 0 -2px;
}

.faq__item.open .faq__arrow {
  transform: rotate(-90deg);
}

.faq__question {
  font-size: 20px;
  line-height: 1.36;
  cursor: pointer;
  font-weight: 700;
  padding: 20px 90px 20px 40px;
}

.faq__ansver {
  font-size: 16px;
  line-height: 1.5;
  display: none;
  padding: 0 40px 25px;
  max-width: 840px;
}

.faq__ansver a {
  color: #230cea;
}

.faq__ansver p {
  margin: 16px 0 0;
}

.faq__ansver p:first-child {
  margin: 0;
}

@media only screen and (max-width: 767px) {
  .faq__arrow {
    top: 24px;
    right: 28px;
  }
  .faq__question {
    font-size: 22px;
    font-weight: 700;
    cursor: pointer;
    padding: 15px 60px 15px 20px;
  }
  .faq__ansver {
    font-size: 14px;
    padding: 0 20px 15px;
  }
}

@media only screen and (max-width: 599px) {
  .faq {
    margin: 0 -30px;
  }
  .faq__question {
    font-size: 18px;
  }
  .faq__section-title {
    font-size: 22px;
    margin: 30px -10px 10px;
  }
}
